import imgAPI from '~/static/images/imgAPI'
import youtube from '~/youtube'
import ParallaxSmall from '../Parallax/Small'
import ParallaxMedium from '../Parallax/Medium'
import Title from '../Title'
import TitleSecondary from '../Title/TitleSecondary'

export default {
  components: {
    ParallaxSmall,
    ParallaxMedium,
    TitleSecondary,
    'title-main': Title
  },
  data() {
    return {
      videoId: 'QFEduQQL9IU',
      imgAPI: imgAPI,
      dialog: false,
      player: null,
      playerVars: {
        autoplay: 0,
        controls: 1,
        rel: 0,
        showinfo: 1,
        mute: 0,
        origin: 'https://localhost:8006'
      },
      yt: youtube,
      footers: [
        {
          title: 'Recommended Scanning Apps',
          description: ['Android', 'iOS'],
          link: [
            'https://play.google.com/store/apps/details?id=com.maqr.barcode.free.qrandbarcodescanner.mavach.qrcode.reader.qrcodereader.qrcodescanner.quickbarecodescanner',
            '#history'
          ]
        }
      ]
    }
  },
  methods: {
    handleVideoOpen() {
      if (!this.yt.use) {
        return false
      }
      this.dialog = true
      setTimeout(() => {
        this.player = this.$refs.youtube.player
        this.player.playVideo()
      }, 100)
    },
    handleVideoClose() {
      this.dialog = false
      this.player.pauseVideo()
    }
  },
  computed: {
    isMobile() {
      const smDown = this.$store.state.breakpoints.smDown
      return smDown.indexOf(this.$mq) > -1
    }
  }
}
