import imgAPI from '~/static/images/imgAPI'
import ParallaxLarge from '../Parallax/Large'
import Title from '../Title'

export default {
  components: {
    'title-main': Title,
    ParallaxLarge,
    Slick: () => import('vue-slick')
  },
  data: () => ({
    imgAPI: imgAPI,
    loaded: false,
    activeSlide: 0,
    settingsCenter: {
      dots: true,
      infinite: true,
      speed: 500,
      fade: true,
      arrows: false,
      asNavFor: '.slider-side'
    },
    settingsSide: {
      autoplay: true,
      autoplaySpeed: 5000,
      dots: false,
      infinite: true,
      speed: 500,
      centerMode: true,
      centerPadding: '10px',
      slidesToShow: 5,
      arrows: false,
      asNavFor: '.slider-center',
      responsive: [
        {
          breakpoint: 800,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1
          }
        }
      ]
    }
  }),
  mounted() {
    this.loaded = true
  },
  methods: {
    handleDotsNav(index) {
      this.$refs.sliderCenter.goTo(index)
    },
    handleActiveSlide(event, slick, currentSlide) {
      this.activeSlide = currentSlide
    }
  }
}
